@import url(https://use.fontawesome.com/d0c316cf02.js);
@import url(https://cdn.jsdelivr.net/npm/marked/marked.js);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&family=Noto+Sans+JP&family=Hammersmith+One&family=Playfair+Display:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0 40px 0 40px;background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAJElEQVQoU2NkIBIwEqmOgTyFq1ev/h8aGopVM3km4nPvUDARAEilBAsfg7AlAAAAAElFTkSuQmCC) repeat;background-attachment:fixed}.main{width:100%;max-height:90vh;display:flex;flex-direction:column;grid-gap:20px;gap:20px}.main .top .title{flex:1 1;padding:0px;padding-top:50px;display:block;font-family:"Playfair Display";font-size:36px;font-weight:200;letter-spacing:-1px;text-align:center}.main .top hr{width:20%}.main .top .sub-title{text-align:center;font-family:"Playfair Display";display:block;font-size:14px}.main .bottom{display:flex;flex-direction:row;grid-gap:50px;gap:50px;flex:5 1}.main .bottom .editor-div{flex:1 1;max-width:100%;max-height:100%}.main .bottom #editor{padding:10px;width:100%;height:70vh;resize:none;flex:1 1;font-size:14px;border:1px solid gray;border-radius:4px;font-family:"Fira Code",monospace}.main .bottom #editor:focus{outline:none}.main .bottom .viewer-div{flex:1 1;max-width:100%;height:100%;font-family:"Noto Sans JP",sans-serif}.main .bottom .viewer-div #preview{border:1px solid gray;border-radius:4px;padding:0px 20px 10px 20px;height:100%;min-height:50px;background-color:#fff}.main .bottom .viewer-div #preview img{max-width:100%}.main .div-desc{font-size:16px;font-family:Noto Sans JP;font-weight:700}
